@font-face {
  font-family: "Oswald";
  src: url("../fonts/oswaldregular.woff2"), url("../fonts/oswaldregular.woff");
  font-weight: 400;
  font-style: normal;
  font-display: swap;
}
@font-face {
  font-family: "Oswald";
  src: url("../fonts/oswaldmedium.woff2"), url("../fonts/oswaldmedium.woff");
  font-weight: 500;
  font-style: normal;
  font-display: swap;
}
:root {
  --container-padding-mobile: 20px;
  --container-padding: 30px;
  --container-width-mobile: (320px - var(--container-padding-mobile) * 2);
  --container-width-tablet: (768px - var(--container-padding) * 2);
  --container-width-desktop: (1280px - var(--container-padding) * 2);
}

img {
  max-width: 100%;
  height: auto;
}

a {
  text-decoration: none;
}

*,
*::before,
*::after {
  box-sizing: border-box;
}

.visually-hidden {
  position: absolute;
  width: 1px;
  height: 1px;
  margin: -1px;
  border: 0;
  padding: 0;
  white-space: nowrap;
  clip-path: inset(100%);
  clip: rect(0 0 0 0);
  overflow: hidden;
}

.container {
  min-width: 320px;
  max-width: 1280px;
  width: 100%;
  margin-left: auto;
  margin-right: auto;
  padding-left: var(--container-padding-mobile);
  padding-right: var(--container-padding-mobile);
}
@media (min-width: 768px) {
  .container {
    padding-left: var(--container-padding);
    padding-right: var(--container-padding);
  }
}

.breakline {
  display: none;
}
@media (max-width: 767px) {
  .breakline--mob {
    display: block;
  }
}

.button {
  display: block;
  font-size: 16px;
  line-height: 20px;
  text-transform: uppercase;
  text-align: center;
  color: #ffffff;
  background-color: #68B738;
  border: none;
  padding: 10px;
  cursor: pointer;
}
.button:hover, .button:active {
  background-color: #5EAA2F;
}
.button:active {
  color: rgba(255, 255, 255, 0.3);
}
.button--secondary {
  color: #444444;
  background-color: #F2F2F2;
}
.button--secondary:hover, .button--secondary:active {
  background-color: #EBEBEB;
}
.button--secondary:hover:active, .button--secondary:active:active {
  color: rgba(68, 68, 68, 0.3);
}

.title {
  font-size: 36px;
  line-height: 36px;
  font-weight: inherit;
}
@media (min-width: 768px) {
  .title {
    font-size: 60px;
    line-height: 60px;
  }
}

.title-section {
  display: inline-block;
  position: relative;
  max-width: 85%;
  font-size: 24px;
  line-height: 26px;
  font-weight: inherit;
}
.title-section::before, .title-section::after {
  content: "";
  position: absolute;
  z-index: -1;
}
.title-section::before {
  top: 0.65em;
  left: calc(var(--container-padding-mobile) * -1);
  height: 2px;
  width: 100vw;
  background-color: #68B738;
}
.title-section::after {
  top: 0;
  left: -20px;
  right: -20px;
  height: 1em;
  background-color: #ffffff;
}
@media (min-width: 768px) {
  .title-section {
    max-width: none;
    font-size: 36px;
    line-height: 36px;
    padding: 0 37px;
  }
  .title-section::before {
    left: calc(var(--container-padding) * -1);
  }
  .title-section::after {
    left: 0;
    right: 0;
  }
}
@media (min-width: 1280px) {
  .title-section {
    padding: 0;
  }
  .title-section::before {
    left: calc((50vw - var(--container-width-desktop) / 2) * -1);
  }
  .title-section::after {
    left: -40px;
    right: -40px;
  }
}

.page {
  height: 100%;
  font-family: "Oswald", "Arial", sans-serif;
  font-weight: 400;
  font-size: 14px;
  line-height: 18px;
}
@media (min-width: 768px) {
  .page {
    font-size: 16px;
    line-height: 24px;
  }
}

.page__body {
  display: grid;
  grid-template-rows: min-content 1fr min-content;
  align-content: start;
  min-height: 100%;
  min-width: 320px;
  color: #000000;
  background-color: #ffffff;
  margin: 0;
  overflow-x: hidden;
}

.page-header {
  border-bottom: 1px solid #EBEBEB;
  z-index: 1;
}
@media (min-width: 768px) {
  .page-header {
    border: none;
  }
}

.page-main__intro {
  margin-bottom: 22px;
}
@media (min-width: 768px) {
  .page-main__intro {
    margin-bottom: 0;
  }
}
@media (min-width: 1280px) {
  .page-main__intro {
    margin-bottom: 82px;
  }
}

.page-main__header {
  padding-top: 26px;
  margin-bottom: 40px;
}
@media (min-width: 768px) {
