    margin-bottom: 70px;
  }
}

.page-main__benefits {
  margin-bottom: 42px;
}
@media (min-width: 768px) {
  .page-main__benefits {
    margin-bottom: 43px;
  }
}
@media (min-width: 1280px) {
  .page-main__benefits {
    margin-bottom: 69px;
  }
}

.page-main__form {
  margin-bottom: 32px;
}
@media (min-width: 768px) {
  .page-main__form {
    margin-bottom: 72px;
  }
}
@media (min-width: 1280px) {
  .page-main__form {
    margin-bottom: 82px;
  }
}

.page-main__catalog {
  margin-bottom: 24px;
}
@media (min-width: 768px) {
  .page-main__catalog {
    padding-top: 30px;
    margin-bottom: 65px;
  }
}
@media (min-width: 1280px) {
  .page-main__catalog {
    margin-bottom: 73px;
  }
}

.page-main__extra-goods {
  margin-bottom: 35px;
}
@media (min-width: 768px) {
  .page-main__extra-goods {
    margin-bottom: 76px;
  }
}
@media (min-width: 1280px) {
  .page-main__extra-goods {
    margin-bottom: 86px;
