.page-footer {
  background-color: #F2F2F2;
}

.page-footer__bottom {
  display: grid;
  padding-top: 45px;
  padding-bottom: 23px;
}
@media (min-width: 768px) {
  .page-footer__bottom {
    grid-template-columns: repeat(3, 1fr);
    column-gap: 30px;
    align-items: center;
    padding-top: 58px;
    padding-bottom: 60px;
  }
}
@media (min-width: 1280px) {
  .page-footer__bottom {
    padding-top: 62px;
  }
}

.page-footer__logo {
  justify-self: center;
  width: 129px;
  height: 25px;
  margin-bottom: 31px;
}
@media (min-width: 768px) {
  .page-footer__logo {
    justify-self: start;
    margin-bottom: 0;
  }
}

.page-footer__socials {
  border-top: 1px solid #CDCDCD;
  border-bottom: 1px solid #CDCDCD;
  padding: 21px;
}
@media (min-width: 768px) {
  .page-footer__socials {
    border: none;
    padding: 0;
  }
}

.page-footer__copywright {
  padding-top: 22px;
}
@media (min-width: 768px) {
  .page-footer__copywright {
    padding: 0;
  }
}

.navigation {
  position: relative;
  display: flex;
  flex-wrap: wrap;
  align-items: center;
  background-color: #ffffff;
  margin-left: calc(var(--container-padding-mobile) * -1);
  margin-right: calc(var(--container-padding-mobile) * -1);
  padding-left: var(--container-padding-mobile);
  padding-right: var(--container-padding-mobile);
  padding-top: 12px;
  padding-bottom: 12px;
  transition: box-shadow 300ms ease-in-out;
}
@media (min-width: 768px) {
  .navigation {
    background-color: transparent;
    margin: 0;
    padding: 0;
    padding-top: 24px;
  }
}
@media (min-width: 1280px) {
  .navigation {
    padding-top: 48px;
    flex-wrap: nowrap;
  }
}
@media (max-width: 767px) {
  .navigation--nojs .navigation__list {
    position: relative;
    left: calc(var(--container-padding-mobile) * -1);
    border-top: 1px solid #EBEBEB;
    margin-top: 13px;
    margin-bottom: -13px;
    transform: none;
    z-index: 0;
  }
  .navigation--nojs .navigation__toggle {
    display: none;
  }
}
@media (max-width: 767px) {
  .navigation--active {
    box-shadow: 0 -10px 20px 0 #000000;
  }
  .navigation--active .navigation__toggle-icon {
    background-color: transparent;
  }
  .navigation--active .navigation__toggle-icon::before {
    transform: rotateZ(45deg) translateY(0);
  }
  .navigation--active .navigation__toggle-icon::after {
    transform: rotateZ(-45deg) translateY(0);
  }
  .navigation--active .navigation__list {
    transform: translateY(1px);
  }
}
@media (min-width: 1280px) {
  .navigation--index .navigation__list {
    max-width: 50%;
    margin-left: auto;
  }
}
@media (min-width: 1280px) {
  .navigation--index .navigation__link {
    color: #ffffff;
    border-bottom-color: #ffffff;
  }
}

.navigation__logo-text {
  display: block;
  width: 102px;
  height: 19px;
  margin-left: auto;
  margin-right: auto;
}
@media (min-width: 768px) {
  .navigation__logo-text {
    display: none;
  }
}

.navigation__toggle {
  display: block;
  width: 35px;
  height: 24px;
  background-color: transparent;
  border: none;
  padding: 0;
  padding-left: 11px;
  cursor: pointer;
}
@media (min-width: 768px) {
  .navigation__toggle {
    display: none;
  }
}

.navigation__toggle-icon {
  display: block;
  width: 100%;
  height: 2px;
  background-color: #000000;
  transition: all 300ms ease-in-out;
}
.navigation__toggle-icon::before, .navigation__toggle-icon::after {
  content: "";
  display: block;
  width: 100%;
  height: 2px;
  background-color: #000000;
  transition: all 300ms ease-in-out;
}
.navigation__toggle-icon::before {
  transform: translateY(-8px);
}
.navigation__toggle-icon::after {
  transform: translateY(8px);
  margin-top: -2px;
}

.navigation__list {
  position: absolute;
  list-style: none;
  min-width: 100vw;
  top: 100%;
  left: 0;
  margin: 0;
  padding: 0;
  transform: translateY(-100%);
  transition: transform 500ms ease-in-out;
  z-index: -1;
}
@media (min-width: 768px) {
  .navigation__list {
    position: static;
    min-width: auto;
    flex-grow: 1;
    display: flex;
    flex-wrap: wrap;
    justify-content: flex-end;
    padding-top: 20px;
    transform: none;
    transition: none;
    z-index: 0;
  }
}
@media (min-width: 1280px) {
  .navigation__list {
    padding-top: 17px;
  }
}

.navigation__item {
  border-bottom: 1px solid #EBEBEB;
}
@media (min-width: 768px) {
  .navigation__item {
    border: none;
    margin-right: 35px;
  }
  .navigation__item:last-child {
    margin: 0;
  }
}

.navigation__link {
  display: block;
  color: #000000;
  font-size: 20px;
  line-height: 20px;
  font-weight: 500;
  text-transform: uppercase;
  text-align: center;
  background-color: #ffffff;
  padding: 22px;
  padding-bottom: 23px;
}
@media (min-width: 768px) {
  .navigation__link {
    position: relative;
    font-size: 18px;
    font-weight: 400;
    background-color: transparent;
    padding: 0;
    padding-bottom: 10px;
  }
  .navigation__link:hover {
    opacity: 0.6;
  }
  .navigation__link:active {
    opacity: 0.3;
  }
}
@media (min-width: 1280px) {
  .navigation__link {
    font-size: 20px;
    line-height: 20px;
  }
}
.navigation__link--current {
  background-color: #F2F2F2;
  pointer-events: none;
}
@media (min-width: 768px) {
  .navigation__link--current {
    background-color: transparent;
    border-bottom: 2px solid #68B738;
  }
}

@media (min-width: 768px) {
  .logo {
    display: block;
  }
  .logo::before {
    display: none;
  }
  .logo:hover {
    opacity: 0.8;
  }
  .logo:active {
    opacity: 0.6;
  }
  .logo:not([href]) {
    pointer-events: none;
  }
}

.logo__image {
  display: block;
  min-width: 35px;
  height: 38px;
}
@media (min-width: 768px) {
  .logo__image {
    min-width: 174px;
    height: 52px;
  }
}
@media (min-width: 1280px) {
  .logo__image {
    min-width: 202px;
    height: 59px;
  }
}

.intro-index {
  position: relative;
  display: grid;
  align-items: start;
  color: #ffffff;
  text-align: center;
  padding-top: 27px;
}
.intro-index::before, .intro-index::after {
  content: "";
  position: absolute;
  top: 0;
  left: 0;
  width: 100%;
  height: 302px;
  z-index: -2;
}
.intro-index::before {
  background-color: #444444;
  background-image: url("../img/intro/index-background-mobile.jpg");
  background-image: image-set(url("../img/intro/index-background-mobile.avif") type("image/avif"), url("../img/intro/index-background-mobile.webp") type("image/webp"));
  background-repeat: no-repeat;
  background-position: center;
  background-size: contain;
}
@media (min-resolution: 2dppx) {
  .intro-index::before {
    background-image: url("../img/intro/index-background-mobile@2x.jpg");
    background-image: image-set(url("../img/intro/index-background-mobile@2x.avif") type("image/avif"), url("../img/intro/index-background-mobile@2x.webp") type("image/webp"));
  }
}
.intro-index::after {
  background-color: rgba(104, 183, 56, 0.85);
}
@media (min-width: 768px) {
  .intro-index {
    grid-template-columns: 1fr 1fr;
    column-gap: 60px;
    color: #000000;
    text-align: left;
    padding-top: 62px;
  }
  .intro-index::before, .intro-index::after {
    display: none;
  }
}
@media (min-width: 1280px) {
  .intro-index {
    position: static;
    padding-top: 29px;
    padding-bottom: 54px;
  }
  .intro-index::before, .intro-index::after {
    display: block;
    width: 50%;
    height: 689px;
    left: auto;
    right: 0;
  }
  .intro-index::before {
    background-image: url("../img/intro/index-background-desktop.jpg");
    background-image: image-set(url("../img/intro/index-background-desktop.avif") type("image/avif"), url("../img/intro/index-background-desktop.webp") type("image/webp"));
    background-size: cover;
    background-position: 0 0;
  }
}
@media (min-width: 1280px) and (min-resolution: 2dppx) {
  .intro-index::before {
    background-image: url("../img/intro/index-background-desktop@2x.jpg");
    background-image: image-set(url("../img/intro/index-background-desktop@2x.avif") type("image/avif"), url("../img/intro/index-background-desktop@2x.webp") type("image/webp"));
  }
}

.intro-index__title {
  margin: 0;
  margin-bottom: 25px;
}
@media (min-width: 768px) {
  .intro-index__title {
    grid-column: 1/-1;
    margin-bottom: 43px;
