/* Got it from there https://css-tricks.com/styling-cross-browser-compatible-range-inputs-css/ */
.input-range {
  appearance: none;
  width: 100%;
  background: transparent;
  margin: 0;
  padding: 0;
}
.input-range::-webkit-slider-thumb {
  width: 34px;
  height: 34px;
  background-color: #ffffff;
  border-radius: 50%;
  border: 1px solid #CDCDCD;
  outline: 4px solid #68B738;
  outline-offset: -18px;
  box-shadow: 0 1px 3px 0 rgba(0, 0, 0, 0.16);
  transition: box-shadow 100ms ease-in-out;
  cursor: pointer;
  appearance: none;
  margin-top: -14px;
}
.input-range::-webkit-slider-thumb:hover {
  box-shadow: 0 4px 4px 0 rgba(0, 0, 0, 0.25);
  cursor: grab;
}
.input-range::-webkit-slider-thumb:active {
  border-color: #68B738;
  cursor: grabbing;
}
.input-range::-moz-range-thumb {
  width: 34px;
  height: 34px;
  background-color: #ffffff;
  border-radius: 50%;
  border: 1px solid #CDCDCD;
  outline: 4px solid #68B738;
  outline-offset: -18px;
  box-shadow: 0 1px 3px 0 rgba(0, 0, 0, 0.16);
  transition: box-shadow 100ms ease-in-out;
  cursor: pointer;
}
.input-range::-moz-range-thumb:hover {
  box-shadow: 0 4px 4px 0 rgba(0, 0, 0, 0.25);
  cursor: grab;
}
.input-range::-moz-range-thumb:active {
  border-color: #68B738;
  cursor: grabbing;
}
.input-range::-webkit-slider-runnable-track {
  width: 100%;
  height: 6px;
  background-color: #CDCDCD;
  border-radius: 6px;
  margin: 0;
}
.input-range::-moz-range-track {
  width: 100%;
  height: 6px;
  background-color: #CDCDCD;
  border-radius: 6px;
  margin: 0;
}
.input-range:focus {
  outline: none;
}
.input-range:focus::-webkit-slider-thumb {
  border-color: #68B738;
}
.input-range:focus::-moz-range-thumb {
  border-color: #68B738;
}

